import { createSelector } from 'reselect'

export const getLang = state => state.global.language

export const getAuth = state => state.global.auth

export const pageTitle = state => state.global.pageTitle

export const getPageTitle = createSelector(pageTitle, name => {
  switch (name) {
    case 'home':
      return ''
    case 'orders':
      return ''
    case 'schedule':
      return ''
    case 'trips':
      return ''
    case 'vehicles':
      return ''
    case 'staff':
      return ''
    case 'reports':
      return ''
    case 'carrier':
      return ''

    default:
      break
  }
})
